@font-face {
    font-family: "tenada";
    font-weight: normal;
    src: url("./fonts/Tenada.ttf") format("truetype");
  }

#GZ4zs {
    --scrolling-color: rgba(255, 255, 255, 0.2);
    background: rgba(0,0,0,.15);
    padding: 10px;
    color: #e0e0e0;
    margin-top: 40px;
  }
  
  .scrollingContainer {
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
    background-color: transparent;
    font-family: 'tenada';
    font-weight: 400;
    font-size: 40px;
    margin: 10px;
  }
  
  .firstItem {
    animation-duration: 50s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  
  .firstItem {
    animation-name: scrollLeft;
  }
  
  /* 호버 시 애니메이션 일시 정지 */
  #GZ4zs:hover .scrollingItem {
    animation-play-state: paused;
  }
  
  /* 스크롤 애니메이션 */
  @keyframes scrollLeft {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  

  
  .col-dz-3 {
    width: 20%;

  }
  
  .padding {
    width: 100%;
    min-height: 1px;
  }
  
  .animated {
    opacity: 1;
    visibility: visible;
    animation-duration: 0.7s;
  }
  
  .fadeInUp {
    animation-name: fadeInUp;
  }
  
  @keyframes fadeInUp {
    from {
      transform: translate3d(0, 40px, 0);
      opacity: 0;
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }
  
  .text_bg_color {
    width: 100%;
    height: 100%;
    background-color: rgba(222, 222, 222, 0.51);
    border-radius: 16px;
  }
  
  .text-table {
    display: table;
    flex-direction: column;
    justify-content: flex-start;
    height: 100px;
    width: 100%;
    background-color: #ffffff60;
    padding: 40px 40px 40px 40px;
    border-radius: 16px;
    border : solid 1px #fff;

  }
  
  .text-table div {
    display: table-cell;
    vertical-align: middle;
  }

  .text-table p {
    display: flex;
    justify-content: flex-start;
  }

  .text-table img {
    width: 205px;
    height: 160px;
  }
  
  .fade_link {
    display: block;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  
  .doz_row {
    padding-left: 3px;
    padding-right: 3px;
    border-radius: 16px;

  }

  .widget_text_wrap {
    padding: 0;
  }

  .inside {
    max-width: fit-content;
    padding-left: 0;
    padding-right: 0;
    margin: 0 auto;
    position: relative;
    border-width: 0;
    border-style: solid;
    display: flex;
    border-radius: 16px;
  }

  .widget_text_wrap {
    background-size:cover;
    background-position:center center;
    width: 100%;
    word-wrap: break-word;
  }

  .widget_text_wrap p {
    margin: 0;
  }
  .widget_text_wrap > div {
    width:100%;
  }

  