@font-face {
  font-family: "GmarketSans";
  font-weight: normal;
  src: url("./fonts/GmarketSansTTFBold.ttf") format("truetype");
}

.App {
  text-align: center;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.Style_slimeImage img {
  animation-fill-mode: forwards;
}

.App-header {
  background-color: rgb(92, 151, 195);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  display: grid;
  position: relative;
}

.Style_slimeButton {
  width: 100%;
  height: 100%;
  background-color: transparent;
}

.Style_slimeText {
  font-size: var(--fontSizeDesktop, 34px);
  font-weight: 800;
}

.Style_slimeTextWrap {
  float: inline-start;
  margin: 0;
  position: relative;
  outline: none;
  overflow-y: inherit;
  -moz-tab-size: 4;
  tab-size: 4;
  white-space: pre-wrap;
  word-wrap: break-word;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0
}

:root {
  --background-main: rgb(92, 151, 195);
  --background-sub: #ffffff;
  --text-sub: #ffffff;
  --brand-main: #9a71fe;
  --cell-gap-size: 10px;
  --cell-max-width: 20px;
  --button-color: #282c34;
  --button-background: #5dbbfe;
  --text-main: #333333;
  --brand-sub: rgb(32, 32, 32);
}

body {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  background-color: var(--background-main);
}

.Style_grid {
  display: grid;
  gap: var(--cell-gap-size);
  grid-template-columns: minmax(20px, 1fr) repeat(48, minmax(0, var(--cell-max-width))) minmax(20px, 1fr);

}

.Style_slime {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Style_slimeImage img {
  display: block;
  width: 100%;
  height: 100%;
}

.Style_slimeText h1, .Style_slimeText h2, .Style_slimeText h3 {
  color: var(--text-sub);
  line-height: 1.5;
}

.Style_slimeText h3 {
  display: flex;
  justify-content: left;
  align-items: flex-start;
}

.highlight {
  background-color: var(--brand-main);
}

.Style_fieldWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Style_formColumn input {
  width: 100%;
  padding: 10px;
  border: 2px solid var(--text-sub);
  border-radius: 50px;
  background-color: transparent;
  color: var(--text-sub);
}

.Style_formSubmitWrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.Style_formSubmit {
  padding: 12px 24px;
  background-color: var(--brand-main);
  color: white;
  border-radius: 50px;
  cursor: pointer;
}

.Style_formSubmit:hover {
  background-color: #ff4500;
}

.button1st {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 100px;
  background-color: #131158;
  transition: background-color 0.3s ease;
}
.button1st:hover {
  background-color: #000000;
}

.button2nd {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 100px;
  background-color: rgba(183, 95, 248, 0.6);
  transition: background-color 0.3s ease;
}
.button2nd:hover {
  background-color: #000000;
}

.button3rd {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 100px;
  background-color: rgba(216, 189, 129, 1);
  transition: background-color 0.3s ease;
}
.button3rd:hover {
  background-color: #000000;
}

.button4th {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 100px;
  background-color: rgba(255, 116, 116, 1);
  transition: background-color 0.3s ease;
}
.button4th:hover {
  background-color: #000000;
}

.Style_slime.off {
  opacity: 0;
}

/* Visible elements */
.Style_slime.on {
  opacity: 1;
  animation: slideBottom 1s ease-in-out forwards;
}

/* Shape styles */
.Style_slimeShape.square {
  background-color: #ffffff80;
  border-radius: 32px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Image styles */
.Style_slimeImage {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

/* Image styles dumbel */
.Style_slimeImageDumbel {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  transform: rotate(-45deg);
}

/* Text styles */
.Style_slimeText {
  font-family: 'Noto Sans KR', sans-serif;
  color: var(--text-sub);
  font-size: 24px;
}

/* Animations */
@keyframes slideBottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.Style_slimePage {
  opacity: 1;
  animation: slideLeft 1.5s ease-in-out forwards;
}

@keyframes slideLeft {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.Style_slimeButtonLink {
  display: inline-block;
  padding: 10px;
  background-color: #000000;
  color: var(--text-main);
  text-decoration: none;
  border-radius: 10px;
}

/* Grid setup */
.Style_gridLink {
  display: grid;
  gap: var(--cell-gap-size);
  grid-template-columns: minmax(20px, 1fr) repeat(48, minmax(0, var(--cell-max-width))) minmax(20px, 1fr);
  grid-template-rows: repeat(35, var(--cell-max-width));
}

/* Shape with animation */
.Style_slimeLink.off {
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Style_slimeLink.on {
  opacity: 1;
  animation: slideLeftLink 1.5s ease-in-out forwards;
}

/* Shape styles */
.Style_slimeShapeLink.square {
  background-color: #FFFFFF1A;
  border-radius: 100px;
  border: solid 1px #FFFFFF;
  width: 100%;
  height: 100%;
}

/* Button styles */
.Style_slimeButtonLink {
  width: 100%;
  height: 100%;
  background-color: var(--background-main);
  transition: background-color 0.3s ease;
  display: inline-block;
  padding: 10px;
  color: var(--text-main);
  text-decoration: none;
  border-radius: 100px;
  object-fit: contain;
}

.Style_slimeButtonLink:hover {
  background-color: var(--brand-main);
  color: white;
}

.Style_slimeTextWrapLink {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.Style_slimeTextLink {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: #ffffff;
  object-fit: contain;
}

/* Image styles */
.Style_slimeImageLink {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Text styles */
.Style_slimeTextWrapLink {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Style_slimeTextLink h2 {
  font-size: 32px;
  color: var(--text-aux);
  text-align: center;
}

/* Animations */
@keyframes slideLeftLink {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Grid setup for header section */
#Ut69Y {
  background-color: rgb(92, 151, 195);
  border-style: solid;
  border-width: 0px 0px 1px;
  border-top-color: rgb(4, 0, 0);
  border-bottom-color: var(--text-sub);
  border-left-color: rgb(4, 0, 0);
  border-right-color: rgb(4, 0, 0);
  display: grid;
  gap: var(--cell-gap-size);
  grid-template-columns: 1fr auto; /* Two columns: 1fr for platform, auto for email/github */
  align-items: center;
  padding: 10px;
}

/* Style for platform text aligned to the left */
.platform-text {
  justify-self: start; /* Align text to the left */
}

/* Style for buttons (email and GitHub) aligned to the right */
#emaillink,
#gitLink {
  justify-self: end; /* Align buttons to the right */
  margin-left: 20px; /* Add spacing between email and GitHub */
}

/* Button and text styling */
.Style_slimeTextWrapHeader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.Style_slimeTextHeader {
  text-align: center;
  color: var(--background-sub);
  font-weight: 800;
  font-size: 14px;
}

.Style_slimeButtonHeader {
  border-radius: 18px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--background-sub);
  text-align: center;
  padding: 3px 15px;
  text-decoration: none;
  background-color: rgba(197, 124, 255, 0);
  color: var(--background-sub);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.Style_slimeButtonHeader:hover {
  background-color: var(--background-sub);
}

.Style_slimeHeader {
  opacity: 1;
  animation: fade 1s ease-in-out forwards;
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  align-items: center;
}

#tpcHy {
  grid-area: 58 / 24 / 62 / 28;
  --icon-color: #ffffff;
  --icon-size: 30;
}

.slimeContainer {
  width: 100%;
  height: 100%;
  justify-content: center;
  text-align: center;
  margin: 40px;
  color: #ffffff;
  padding-top: calc(36 * var(--site-ratio) * 1px);
  padding-bottom: calc(36 * var(--site-ratio) * 1px);
  padding-left: calc(36 * var(--site-ratio) * 1px);
  padding-right: calc(36 * var(--site-ratio) * 1px);
  background-color: transparent;
  box-shadow: none;
}

#tpcHy.off {
  opacity: 0;
}

#tpcHy.on {
  animation: scale 1s ease-in-out forwards;
}

/* 기존 스타일 위에 추가 */
@keyframes scale {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

